import React from 'react'
import { Card, Box } from 'theme-ui'
import MemphisPattern from '@components/MemphisPattern'
import Stats from '@widgets/Stats'
import HeroBoxed from '@widgets/HeroBoxed'
import Content from './Hero.Content'
import Photo from './Hero.Photo'
import Section from '@components/Section'

const styles = {
  stats: {
    display: [`none`, null, `block`],
    borderRadius: `left`,
    overflow: `hidden`,
    position: `absolute`,
    right: 0,
    bottom: 5,
    mb: -3
  },
  pattern: {
    width: `3/4`,
    opacity: 0.1,
    left: 0,
    right: `auto`,
    bottom: 0
  },
  wrapper: {
    display: `flex`,
    alignItems: `flex-end`,
    position: `relative`,
    //mt: [0, null, 0]
  },
}

export const Hero = () => (
  <Section>
    <Card variant='paper-lg' sx={styles.wrapper}>
    <MemphisPattern sx={styles.pattern} />
    <HeroBoxed.LeftColumn>
      <Content />
    </HeroBoxed.LeftColumn>
    <HeroBoxed.RightColumn>
      <Photo />
    </HeroBoxed.RightColumn>
    <Box sx={styles.stats}>
      <Stats />
    </Box>
    </Card>
  </Section>
)
