import React from 'react'
import { Box, Flex } from 'theme-ui'
import Drawer from '@solid-ui-components/Drawer'
import ContentButtons from '@solid-ui-components/ContentButtons'


import menuItems from "./Menu.Items.jsx"

const styles = {
  desktopMenu: {
    display: [`none`, null, `block`],
    minWidth: `auto`,
    flexGrow: 1,
    '.button-group-link.level-2, .button-group-link.level-3': {
      color: `omegaDark`,
    }, 
    '.button-group-link.level-2:hover, .button-group-link.level-3:hover' : {
      color: `alpha`
    }
  },
  mobileMenu: {
    display: [`block`, null, `none`],
  }
}

const DesktopMenu = ({collection}) => {
  const menuJustify = 'flex-end'
  return (
    <>
      <Box>
          <Flex
            sx={{
              alignItems: `center`,
              justifyContent: menuJustify,
            }}
          >
            {collection.map(
              ({ buttons }, index) =>
                buttons && (
                  <Box
                    key={`item-${index}`}
                    sx={{
                      '& + &': {
                        ml: 4
                      }
                    }}
                  >
                    <ContentButtons content={buttons} />
                  </Box>
                )
            )}
          </Flex>
      </Box>
    </>
  )
}

const MobileMenu = ({collection}) => {
  return (
    <Box>
      <Drawer buttonStyle={{ svg: { size: 32 } }}>
        {collection.map(
          ({ buttons }, index) =>
            buttons && (
              <Box
                key={`item-${index}`}
                sx={{
                  fontSize: 3,
                  '.button-group-link.level-2, .button-group-link.level-3': {
                    color: `omegaDark`
                  }, 
                  '.button-group-link.level-2:hover, .button-group-link.level-3:hover' : {
                    color: `alpha`
                  }
                }}
              >
                <ContentButtons
                  content={buttons}
                  variant='vertical'
                />
              </Box>
            )
        )}
      </Drawer>
    </Box>
  )
}


export const HeaderMenu = ({ mobileMenu = {} }) => {
  const desktopMenuNav = DesktopMenu({collection: menuItems})
  const mobileMenuNav = MobileMenu({collection: menuItems})
  return (
    <>
      <Box sx={styles.desktopMenu}>{desktopMenuNav}</Box>
      <Box sx={styles.mobileMenu}>{mobileMenuNav}</Box>
    </>
  )
}
