import React from 'react'
import { Link } from 'gatsby'
import { Box, Heading, Button } from 'theme-ui'
import Section from '@components/Section'

/**
 * Shadow me to add your own content
 */

const styles = {
  heading: {
    mb: 4,
    span: {
      color: `omegaDark`
    }
  },
  running: {
    color: `omegaDark`,
    fontWeight: `body`,
    width: `5/6`
  }
}

export default () => (
  <>
    <Section>
      <Heading variant='h1' sx={styles.heading}>
        EPI2ME: bioinformatics for all levels of expertise
      </Heading>
      <Heading variant='h3' sx={styles.running}>
        EPI2ME breaks the bioinformatics paradigm by enabling anyone
        to analyse their own data.
      </Heading>
    </Section>
    <Box variant='buttons.group' sx={styles.buttons}>
      <Button as={Link} to='/about'>
        EPI2ME Desktop
      </Button>
      <Button variant='white' as={Link} to='/wfindex'>
        Explore Workflows
      </Button>
      <Button variant='white' as={Link} to='/dataindex'>
        Download Datasets
      </Button>
    </Box>
  </>
)
