import React, { useContext } from 'react'
import { Container, Box, Flex } from 'theme-ui'
import pageContextProvider from '@helpers/pageContextProvider'
import Search from '@widgets/Search'
import { HeaderLogo } from '@layout/Header/Header.Logo'
import { HeaderMenu } from '@layout/Header/Header.Menu'
import { HeaderColorMode } from '@layout/Header/Header.ColorMode'

// for cookie consent
import { Helmet } from "react-helmet";
import CookieConsent, { getCookieConsentValue } from 'react-cookie-consent';
import { useState, useEffect } from "react"
const HEAP_APPID = '2162541267'


const styles = {
  wrapper: {
    position: `relative`,
    bg: `headerBg`
  },
  container: {
    position: `relative`,
    zIndex: 10
  },
  logoContainer: {
    flexBasis: [`full`, null, `1/6`],
    minWidth: `auto`,
  },
  searchContainer: {
    flexBasis: [`auto`, null, `2/6`],
    minWidth: `auto`,
    order: [3, null, `unset`],
    mx: 3
  },
  menuContainer: {
    flexBasis: [`auto`, null, `4/6`],
    minWidth: `auto`,
    order: [4, null, `unset`]
  },
  colorModeContainer: {
    minWidth: `auto`,
    order: [2, null, `unset`]
  }
}

export const Header = ({ children }) => {
  const context = useContext(pageContextProvider)

  const { services, mobileMenu, darkMode } = context.pageContext

  const algolia = services && services.algolia

  // loading heap, simple react effect which will fire on page load as well as when state updated
  const [loadHeap, setLoadHeap] = useState(0);
  useEffect(() => {
    const useHeap =`${process.env.GATSBY_USE_HEAP}` === "true"
    if (getCookieConsentValue() && useHeap && window.heap !== undefined && window.heap.userId === undefined) {
      console.log("Enabling heap") 
      window.heap.load(HEAP_APPID);
    }
  });

  return (
      <Box sx={styles.wrapper}>
      <Helmet>
        <script type="text/javascript">{`
            window.heap=window.heap||[],heap.load=function(e,t){window.heap.appid=e,window.heap.config=t=t||{};var r=document.createElement("script");r.type="text/javascript",r.async=!0,r.src="https://cdn.heapanalytics.com/js/heap-"+e+".js";var a=document.getElementsByTagName("script")[0];a.parentNode.insertBefore(r,a);for(var n=function(e){return function(){heap.push([e].concat(Array.prototype.slice.call(arguments,0)))}},p=["addEventProperties","addUserProperties","clearEventProperties","identify","resetIdentity","removeEventProperty","setEventProperties","track","unsetEventProperty"],o=0;o<p.length;o++)heap[p[o]]=n(p[o])};
        `}</script>
      </Helmet>
      <CookieConsent
        location="none"
        overlay
        buttonText="Accept all"
        enableDeclineButton={true}
        declineButtonText="Required only"
        buttonStyle={{
          color: "white", 
          background: "#7CBAB7",
          fontWeight: "bold"
        }}
        declineButtonStyle={{
          color: "#7CBAB7",
          background: "#115571",
          borderRadius: "1px", 
          border: "1px solid #7CBAB7"
        }}
        style={{
          position: "fixed", zIndex: "999",
          background: "#115571",
          display:"flex",
          left: "auto", bottom: 20,
          right: 20,
          maxWidth: "300px",
          height: "fit-content",
          padding: "4px 14px",
          justifyContent: "space-between",
          borderRadius: "15px",
          boxShadow: "0 0 4px 2px rgba(255, 255, 255, 0.2)"
        }}
        contentStyle={{
            flex: "1 0 auto",
            width: "80%"
        }}
        onAccept={() => {
          setLoadHeap(loadHeap + 1);
        }}>
      <h3>Our website uses cookies</h3>This website uses cookies to track user interactions in order to improve user experience.
      </CookieConsent>


      <Container variant='compact' sx={styles.container}>
        <Flex variant='layout.header'>
          <Box sx={styles.logoContainer}>
            <HeaderLogo />
          </Box>
          <Box sx={styles.menuContainer}>
            <HeaderMenu mobileMenu={mobileMenu} />
          </Box>
          <Box sx={styles.searchContainer}>{algolia && <Search />}</Box>
          <Box sx={styles.colorModeContainer}>
            {darkMode && <HeaderColorMode />}
          </Box>
        </Flex>
      </Container>
      {children}
    </Box>
  )
}
